	module.exports = {"data":{"markdownRemark":{"html":"<p><a href=\"http://blogs.technet.com/b/microsoft_on_the_issues/archive/2013/08/15/the-limits-of-google-s-openness.aspx\">From Microsoft’s official “Microsoft on the Issues” Blog:</a></p>\n<blockquote>\n<p>It seems to us that Google’s reasons for blocking our app are manufactured so that we can’t give our users the same experience Android and iPhone users are getting. The roadblocks Google has set up are impossible to overcome, and they know it.</p>\n</blockquote>\n<p>It’s ok to have an open API, and it’s ok to not have an open API so that your competitors can’t benefit from your services, but claiming to have an open API and then shutting out users of a competitors platforms on a string of technicalities? That’s just a joke.</p>","frontmatter":{"title":"Somewhat Open","keywords":null,"category":"opinion","date":"2013-08-15T16:15:00+00:00","path":"/2013/08/15/somewhat-open","layout":"post","hideFooter":null},"fields":{"slug":"/2013/08/15/somewhat-open"}}},"pathContext":{"slug":"/2013/08/15/somewhat-open","relatedPosts":[{"path":"/2013/06/09/90-done-halfway-there","data":{"title":"90% Done, Halfway There","path":"/2013/06/09/90-done-halfway-there","description":"A post on the difficulties of estimating development projects","date":"2013-06-09T18:45:00+00:00"}},{"path":"/2013/01/25/career-fairs-how-to-not-get-hired-and-how-to-give-yourself-a-chance","data":{"title":"Career Fairs: How To Not Get Hired (And How To Give Yourself A Chance)","path":"/2013/01/25/career-fairs-how-to-not-get-hired-and-how-to-give-yourself-a-chance","description":"What to do when you want a job","date":"2013-01-25T19:30:00+00:00"}},{"path":"/2013/06/08/customer-culture-revisited","data":{"title":"Customer Culture Revisited","path":"/2013/06/08/customer-culture-revisited","description":"Talking about how Microsoft culture impacts their products","date":"2013-06-08T18:45:00+00:00"}}]}}

